.link {
  position: relative;
  font-weight: 700;
  line-height: 150%;
  letter-spacing: 3px;
  text-transform: uppercase;

  &::after {
    display: block;
    content: "";
    position: absolute;
    top: 28px;
    width: 100%;
    height: 2px;
    border-radius: 4px;
    background-color: $blue-color;
  }

  &--pink {
    position: relative;
    font-weight: 700;
    line-height: 150%;
    letter-spacing: 3px;
    text-transform: uppercase;

    &::after {
      display: block;
      content: "";
      position: absolute;
      top: 28px;
      width: 100%;
      height: 2px;
      border-radius: 4px;
      background-color: $light-pink-color;
    }
  }
}
